@import url("../font/Font.css");

.banner_box{
    overflow: hidden;
    display: table-cell;
    background-color:red;
    width : 700px;
    height : 350px;
    background-image: url('');
    color : white;
    font-size: 50px;
    text-align: center;
    vertical-align: middle;
    font-family: "BMHANNA_11yrs";
}

.img_select{
    border: 1px solid black;
}

.img_option{
    padding: 5%;
}

.img_toggle{
    border: 1px solid grey;
}

.input_box{
    position: relative;
    margin: 2rem;
}

.all{
    position: absolute;
    top:60%;
    left: 50%;
    height: 700px;
    width: 700px;

    margin:-350px 0px 0px -350px;
}
.font_color{
    border : 1px solid black;
    margin-top : 2%;
    margin-bottom: 2%;
    padding-left: 1%;
    padding-top:2%;
    padding-bottom: 2%;
}